import React from 'react'
import { NavLink } from 'react-router-dom'
// import { useGet } from '../../hooks/get.hook'
import Styles from './News.module.css'

import News1 from './../../assets/news/news1.png'
import News2 from './../../assets/news/news2.png'
import News3 from './../../assets/news/news3.png'

export const News = () => {
    // const { data, loading } = useGet('/api/news')

    const news = [
        { imageUrl: News1, heading: '  ', date: '21 , 2021', description: '    !          -.      !' },
        { imageUrl: News2, heading: '    ', date: '12 , 2021', description: '       .             .      6   .' },
        { imageUrl: News3, heading: '     ', date: '20 , 2020', description: '     2009 ,      3 830 556 ,     ,   ,   271 187  (   212 063  ).' },
    ]

    return (
        <div className={Styles.news}>
            <div className="container">
                <h3 className={Styles.heading}> </h3>
                {
                    <div className={Styles.block}>
                        {
                            news.map(({ imageUrl, heading, date, description }, i) => {
                                return (
                                    <div key={ i } className={Styles.item}>
                                        <img src={ imageUrl } alt={ heading } />
                                        <div className={Styles.text}>
                                            <p className={Styles.title}>{ heading }</p>
                                            <p className={Styles.date}>{ date }</p>
                                            <p className={Styles.description}>{ description }</p>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                }
                <div className={Styles.button}>
                    <NavLink to="/news"> </NavLink>
                </div>
            </div>
        </div>
    )
}
