import React, { useEffect } from 'react'
import Styles from './StaffPage.module.css'

import Dots from './../../assets/images/dots1.png'

import Face1 from './../../assets/staff/face4.png'
import Face2 from './../../assets/staff/face5.png'
import Face3 from './../../assets/staff/face6.png'
import Face4 from './../../assets/staff/face7.png'
import Face5 from './../../assets/staff/face1.png'
import Face6 from './../../assets/staff/face2.png'
import Face7 from './../../assets/staff/face3.png'
import Face8 from './../../assets/staff/face8.png'
import Face9 from './../../assets/staff/face9.png'
import Face10 from './../../assets/staff/face10.png'
import Face11 from './../../assets/staff/face11.png'
import Face12 from './../../assets/staff/face12.png'
import Face13 from './../../assets/staff/face13.png'
import Face14 from './../../assets/staff/face14.png'
import Face15 from './../../assets/staff/face15.png'
import Face16 from './../../assets/staff/face16.png'
import Face17 from './../../assets/staff/face17.png'
import Face18 from './../../assets/staff/face18.png'
import Face19 from './../../assets/staff/face19.png'
import Face20 from './../../assets/staff/face20.png'
import Face21 from './../../assets/staff/face21.png'
import Face22 from './../../assets/staff/face22.png'
import Face23 from './../../assets/staff/face23.png'
import Face24 from './../../assets/staff/face24.png'
import Face25 from './../../assets/staff/face25.png'
import Face26 from './../../assets/staff/face26.png'
import Face27 from './../../assets/staff/face27.png'
import Face28 from './../../assets/staff/face28.png'

export const StaffPage = () => {
    useEffect(() => {
        window.scrollTo(0,0);
        return () => {
            window.scrollTo(0,0);
        }
    })

    const staff = [
        { imageUrl: Face1, fullname: '  ', position: ' ', administration: true },
        { imageUrl: Face2, fullname: '  ', position: '   ', administration: true },
        { imageUrl: Face3, fullname: '  ', position: '    ', administration: true },
        { imageUrl: Face4, fullname: '  ', position: '     ', administration: true },
        { imageUrl: Face27, fullname: '  ', position: ', ', administration: true },
        { imageUrl: Face2, fullname: '  ', position: '    ,    ', administration: false },
        { imageUrl: Face3, fullname: '  ', position: '       ,     .', administration: false },
        { imageUrl: Face4, fullname: '  ', position: '     ,      ', administration: false },
        { imageUrl: Face5, fullname: '  ', position: '      ', administration: false },
        { imageUrl: Face6, fullname: '  ', position: '    ', administration: false },
        { imageUrl: Face7, fullname: '  ', position: '     ,  ', administration: false },
        { imageUrl: Face8, fullname: '  ', position: '  ', administration: false },
        { imageUrl: Face9, fullname: '  ', position: '  ', administration: false },
        { imageUrl: Face10, fullname: '  ', position: ' ,       ', administration: false },
        { imageUrl: Face11, fullname: '  ', position: ' ', administration: false },
        { imageUrl: Face12, fullname: '  ', position: '  ', administration: false },
        { imageUrl: Face13, fullname: '  ', position: '     ,   " -96",   ', administration: false },
        { imageUrl: Face14, fullname: '  ', position: '     ', administration: false },
        { imageUrl: Face15, fullname: '  ', position: '  .  .', administration: false },
        { imageUrl: Face16, fullname: '  ', position: '   ,   .', administration: false },
        { imageUrl: Face17, fullname: '  ', position: ' ', administration: false },
        { imageUrl: Face18, fullname: '  ', position: '  - ,    3 .,   ,     ,      ,   ', administration: false },
        { imageUrl: Face19, fullname: '  ', position: '  ,     ', administration: false },
        { imageUrl: Face20, fullname: '  ', position: '     ,  ', administration: false },
        { imageUrl: Face21, fullname: '  ', position: '  , , ', administration: false },
        { imageUrl: Face22, fullname: '  ', position: '     ', administration: false },
        { imageUrl: Face23, fullname: '  ', position: '  ,   ', administration: false },
        { imageUrl: Face24, fullname: '  ', position: ' - ', administration: false },
        { imageUrl: Face25, fullname: '  ', position: '   ', administration: false },
        { imageUrl: Face26, fullname: '  ', position: '    ', administration: false },
        { imageUrl: Face28, fullname: '  ', position: ' .    .  .', administration: false }
    ]

    return (
        <div className={Styles.staff}>
            <div className="container">
                {
                    <div>
                        <h3 className={Styles.heading}> </h3>
                        <div className={`${Styles.block} ${Styles.fi}`}>
                            {
                                staff.filter(el => el.administration === true).map(({ imageUrl, fullname, position }, i) => {
                                    return (
                                        <div key={ i } className={Styles.item}>
                                            <div className={Styles.image}>
                                                <img src={ imageUrl } alt={ fullname } />
                                                <img src={ Dots } alt="dots" />
                                            </div>
                                            <p className={Styles.fullname}>{ fullname }</p>
                                            <p className={Styles.position}>{ position }</p>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        <h3 className={Styles.heading}> </h3>
                        <div className={Styles.block}>
                            {
                                staff.filter(el => el.administration !== true).map(({ imageUrl, fullname, position }, i) => {
                                    return (
                                        <div key={ i } className={Styles.item}>
                                            <div className={Styles.image}>
                                                <img src={ imageUrl } alt={ fullname } />
                                                <img src={ Dots } alt="dots" />
                                            </div>
                                            <p className={Styles.fullname}>{ fullname }</p>
                                            <p className={Styles.position}>{ position }</p>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                }
            </div>
        </div>
    )
}
